@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: auto;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  font-family: "Orbitron", sans-serif;
}

h3 {
  color: #e1a628;
}

.logo {
  width: 15rem;
}

.container {
  padding: 0;
  position: fixed !important;
  z-index: 1;
}

.container .navList .nav-Link {
  text-align: center;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  color: #e1a628 !important;
}

.container .navList .nav-Link:hover {
  color: #5dc8d3 !important;
  border-bottom: 3px solid #5dc8d3;
}

.container nav.p-4.navbar.navbar-expand-lg.navbar-light {
  left: 0;
  background-color: rgba(15, 15, 19, 0.856);
  width: 100vw;
}

.container .navbarScroll {
  margin-bottom: 2rem;
  text-align: right;
}

@media (max-width: 576px) {
  .container {
    height: -webkit-min-content;
    height: min-content;
  }
}

.Header-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url(/Eman-Portfolio/static/media/Header-background.c956ebd6.jpeg), linear-gradient(to bottom, #0f0f13 20%, #999fa1 50%);
  background-blend-mode: multiply;
}

.Header-section .myTitle {
  color: #e1a628;
  font-size: xx-large;
}

.Header-section .typed-text {
  color: #5dc8d3;
  font-size: larger;
}

@media (max-width: 576px) {
  .Header-section .myTitle {
    font-size: larger;
  }
  .Header-section .typed-text {
    font-size: large;
  }
}

.about-me {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.about-me p {
  font-family: sans-serif;
  width: 50vw;
  line-height: 200%;
}

@media (max-width: 575.98px) {
  .about-me {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-me p {
    width: 70vw;
    height: 50vh;
    overflow-y: scroll;
    font-size: small;
  }
  .about-me img {
    width: 15rem;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .about-me {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-me p {
    width: 70vw;
    height: 50vh;
    overflow-y: scroll;
    font-size: small;
  }
  .about-me img {
    width: 15rem;
  }
}

.projects {
  padding: 3rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-image: url(/Eman-Portfolio/static/media/projects-sec.5ef1003b.jpeg), linear-gradient(to top, #0f0f12 30%, #d3d8da 100%);
  background-blend-mode: multiply;
  background-size: cover;
}

.projects .projects-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media (max-width: 575.98px) {
  .projects .projects-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80vw;
    overflow-y: scroll;
    font-size: small;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .projects .projects-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80vw;
    overflow-y: scroll;
    font-size: small;
  }
}

@media (max-width: 575.98px) {
  .projects {
    height: -webkit-min-content;
    height: min-content;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .projects {
    height: -webkit-min-content;
    height: min-content;
  }
}

.content {
  min-height: "10rem";
}

.content:hover {
  background-color: #5dc8d3;
}

.card-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-height: "2rem";
  color: #808485;
}

.experience {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 3rem;
  font-size: small;
}

.experience .timeLineContainer {
  height: 60vh;
  overflow-y: scroll;
  border-top: 1.5rem solid #5dc8d3;
  border-bottom: 1.5rem solid #5dc8d3;
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.contact {
  height: 10vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #e1a628;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: rgba(15, 15, 19, 0.856);
  color: #5dc8d3;
  padding: 2rem;
}

.footer .footerContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 20vh;
}

.footer .iconsContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.footer .iconsContainer .icon {
  color: #5dc8d3;
}

@media (max-width: 575.98px) {
  .footer {
    font-size: smaller;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .footer {
    font-size: smaller;
  }
}
